import React, { useEffect } from 'react';
import { LinkContainer } from 'react-router-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { Navbar, Nav, NavDropdown } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';

import Badge from 'react-bootstrap/Badge';
import Dropdown from 'react-bootstrap/Dropdown';

import { logout } from '../actions/userActions';
import {
  listNotifications,
  markNotificationRead,
  markAllNotificationsRead,
} from '../actions/notificationActions';

// Nhn socket t props (c truyn t App.js)
export const NavBar = ({ socket }) => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  // USER
  const { userInfo } = useSelector((state) => state.userLogin);

  // CART
  const { cartItems } = useSelector((state) => state.cart);

  // NOTIFICATIONS
  const notificationList = useSelector((state) => state.notificationList);
  const { notifications = [] } = notificationList;

  // Tnh s lng cha c  hin th Badge s 
  const unreadCount = notifications.filter((n) => !n.isRead).length;

  // 1. Fetch danh sch thng bo t Database khi ngi dng ng nhp
  useEffect(() => {
    if (userInfo) {
      dispatch(listNotifications());
    }
  }, [dispatch, userInfo]);

  // 2. Lng nghe s kin Socket  cp nht thng bo thi gian thc
  useEffect(() => {
    if (!socket?.current || !userInfo) return;

    const handleNewNotification = (data) => {
      // Khi c tin nhn mi, khng dng Toast na m gi action  cp nht li danh sch trong Dropdown
      dispatch(listNotifications());

      // Ty chn: Bn c th thm hiu ng m thanh nh ti y
      // const audio = new Audio('/sounds/notification_ping.mp3');
      // audio.play().catch(e => console.log("Audio play failed", e));
    };

    socket.current.on("newNotification", handleNewNotification);

    return () => {
      socket.current.off("newNotification", handleNewNotification);
    };
  }, [socket, userInfo, dispatch]);

  const logoutHandler = () => {
    dispatch(logout());
  };

  const handleNotificationClick = (notification) => {
    // nh du l  c trong Database
    dispatch(markNotificationRead(notification._id));

    // Chuyn hng ngi dng n link lin kt (v d: phng chat)
    if (notification.link) {
      navigate(notification.link);
    }
  };

  return (
    <Navbar expand="lg" variant="dark" bg="dark" fixed="top" className="mb-5">
      <div className="container">
        <LinkContainer to="/">
          <Navbar.Brand>The Shop</Navbar.Brand>
        </LinkContainer>

        <Navbar.Toggle />
        <Navbar.Collapse>
          <Nav className="ms-auto align-items-center">

            {/* PHN NOTIFICATION CA BN */}
            {userInfo && (
              <Dropdown align="end" className="me-3">
                <Dropdown.Toggle
                  variant="dark"
                  id="dropdown-notification"
                  className="position-relative"
                >
                  <i className="fas fa-bell"></i> Notification

                  {/* Hin th s lng tin nhn cha c trn icon chung */}
                  {unreadCount > 0 && (
                    <Badge
                      bg="danger"
                      pill
                      className="position-absolute top-0 start-100 translate-middle"
                    >
                      {unreadCount}
                    </Badge>
                  )}
                </Dropdown.Toggle>

                <Dropdown.Menu
                  className="notification-menu"
                  style={{
                    width: '320px',
                    maxHeight: '400px',
                    overflowY: 'auto',
                  }}
                >

                  {/* Nt nh du tt c  c */}
                  {unreadCount > 0 && (
                    <>
                      <Dropdown.Item
                        className="notification-action text-center fw-bold"
                        onClick={() => dispatch(markAllNotificationsRead())}
                      >
                        <i className="fas fa-check-double me-2"></i>
                        Mark all as read
                      </Dropdown.Item>

                      <Dropdown.Divider />
                    </>
                  )}

                  {/* Trng hp khng c thng bo no */}
                  {notifications.length === 0 && (
                    <Dropdown.Item className="notification-empty text-center">
                      Khng c thng bo mi
                    </Dropdown.Item>
                  )}




                  {/* Hin th danh sch thng bo (Bao gm c tin nhn mi t Socket) */}
                  {notifications.map((n) => (
                    <Dropdown.Item
                      key={n._id}
                      onClick={() => handleNotificationClick(n)}
                      style={{
                        backgroundColor: n.isRead ? '#fff' : '#e7f1ff',
                        fontWeight: n.isRead ? 'normal' : 'bold',
                        whiteSpace: 'normal',
                        borderBottom: '1px solid #eee'
                      }}
                    >
                      <div className="d-flex justify-content-between">
                        <span>{n.title}</span>
                        {!n.isRead && <Badge bg="primary" pill>New</Badge>}
                      </div>
                      <small className="text-muted d-block">{n.message}</small>
                      <div className="text-end text-muted" style={{ fontSize: '0.7rem' }}>
                        {new Date(n.createdAt).toLocaleTimeString()}
                      </div>
                    </Dropdown.Item>
                  ))}
                </Dropdown.Menu>
              </Dropdown>
            )}

            {/* CC MENU KHC GI NGUYN */}
            <LinkContainer to="/chat">
              <Nav.Link>
                <i className="fas fa-comment"></i> Chat
              </Nav.Link>
            </LinkContainer>

            <LinkContainer to="/chatbot">

              <Nav.Link>

                <i className="fas fa-message"></i> Chatbot              </Nav.Link>

            </LinkContainer>

            <LinkContainer to="/cart">
              <Nav.Link>
                <i className="fas fa-shopping-cart"></i>{' '}
                Cart {userInfo && `(${cartItems.reduce((a, c) => a + c.qty, 0)})`}
              </Nav.Link>
            </LinkContainer>

            <LinkContainer to="/discounts">
              <Nav.Link>
                <i className="fas fa-tag"></i> Discounts
              </Nav.Link>
            </LinkContainer>

            {userInfo?.role === 'seller' && (
              <LinkContainer to="/seller/products">
                <Nav.Link>My Products</Nav.Link>
              </LinkContainer>
            )}

            {userInfo && (
              <NavDropdown title="Orders">
                <LinkContainer to="/orders">
                  <NavDropdown.Item>My Orders</NavDropdown.Item>
                </LinkContainer>
                {userInfo.role === 'seller' && (
                  <LinkContainer to="/seller/orders">
                    <NavDropdown.Item>My Sales</NavDropdown.Item>
                  </LinkContainer>
                )}
              </NavDropdown>
            )}

            {userInfo ? (
              <>
                <LinkContainer to="/profile">
                  <Nav.Link>{userInfo.name}</Nav.Link>
                </LinkContainer>
                <Nav.Link onClick={logoutHandler}>Logout</Nav.Link>
              </>
            ) : (
              <LinkContainer to="/login">
                <Nav.Link>
                  <i className="fas fa-user"></i> Sign In
                </Nav.Link>
              </LinkContainer>
            )}

            {userInfo?.isAdmin && (
              <>
                <LinkContainer to="/admin/userlist">
                  <Nav.Link>Shoppers</Nav.Link>
                </LinkContainer>
                <LinkContainer to="/admin/productlist">
                  <Nav.Link>All Products</Nav.Link>
                </LinkContainer>
                <LinkContainer to="/admin/orderlist">
                  <Nav.Link>All Orders</Nav.Link>
                </LinkContainer>
              </>
            )}
          </Nav>
        </Navbar.Collapse>
      </div>
    </Navbar>
  );
};